.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
  border-radius: 50%;
  margin-right: 12px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s ease-in-out;
  }
}

.App-header {
  background-color: #fff;
  min-height: 20vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 5vmin);
  color: #40304A;
}

.App-header h4 {
  margin-top: 36px;
  margin-left: 12px;
  color: #6291BD;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform:  rotate(20deg) ;
  }
  50% {
    transform: rotate(-30deg) ;
  }
  to {
    transform:  rotate(20deg);
  }
}
